import { type } from "@testing-library/user-event/dist/type";
import { useEffect, useState } from "react";
import styles from "./PokemonCard.module.scss";

const PokemonCard = (props) => {
  const [pokemonData, setPokemonData] = useState([]);

  useEffect(() => {
    fetch(props.url)
      .then((response) => response.json())
      .then((actualData) => setPokemonData(actualData));
  }, [props.url]);

  const types =
    pokemonData.types &&
    pokemonData.types.flatMap((type, index) => {
      return [type.type.name];
    });

  console.log(types);

  let fixID;
  if (pokemonData.id < 10) {
    fixID = `00${pokemonData.id}`;
  } else if (pokemonData.id < 100) {
    fixID = `0${pokemonData.id}`;
  } else {
    fixID = pokemonData.id;
  }

  return (
    <div className={`${styles.pokemon_card} ${styles[types && types[0]]}`}>
      <p className={styles.pokemon_card__id}>#{fixID}</p>
      <h1 className={styles.pokemon_card__name}>
        {pokemonData.name &&
          pokemonData.name[0].toUpperCase() + pokemonData.name.slice(1)}
      </h1>
      <img
        src={`https://assets.pokemon.com/assets/cms2/img/pokedex/full/${fixID}.png`}
        alt={pokemonData.name}
      />
      {/* <img
        src={`https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/${pokemonData.id}.png`}
        alt=""
      /> */}
      <div className="pokemon_card__types">
        {types &&
          types.map((type, index) => {
            return (
              <p
                key={index}
                className={`${styles.pokemon_card__type} ${
                  styles[`${type}-label`]
                }`}
              >
                {type && type[0].toUpperCase() + type.slice(1)}
              </p>
            );
          })}
      </div>
    </div>
  );
};

export default PokemonCard;
